.LoginScreen{
    position: relative;
    height: 100%;
    background: url("https://infoguidenigeria.com/wp-content/uploads/2021/10/netflix-library-photo-scaled-1.jpg") center no-repeat;
    background-size: cover;
}

.LoginScreen_logo{
    position: fixed;
    left: 0;
    width: 150px;
    object-fit: contain;
    padding-left: 20px;
}

.LoginScreen_gradient{
    width: 100%;
    z-index: 1;
    height: 100vh;
    background: rgba(0, 0, 0, 0.4);
    background-image: linear-gradient(
        to top,
        rgba(0, 0, 0, 0.8) 0,
        rgba(0, 0, 0, 0) 60%,
        rgba(0, 0, 0, 0.8) 100%
        );
}

.LoginScreen_button{
    position: fixed;
    right: 20px;
    top: 20px;
    padding: 10px 20px;
    font-size: 1rem;
    color: #fff;
    background-color: #e50914;
    font-weight: 600;
    border: none;
    cursor: pointer;
}

.LoginScreen_body{
    position: absolute;
    z-index: 1;
    top: 30%;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    color: #fff;
    padding: 20px;
    left: 0;
    right: 0;
}

.LoginScreen_body > h1{
    font-size: 3.125rem;
    margin-bottom: 20px;
}

.LoginScreen_body > h2{
    font-size: 2rem;
    font-weight: 400;
    margin-bottom: 30px;
}

.LoginScreen_body > h3{
    font-size: 1.3rem;
    font-weight: 400;
}

.LoginScreen_input > form > input{
    padding: 10px;
    outline-width: 0;
    height: 30px;
    width: 30%;
    border: none;
    max-width: 600px;
}

.LoginScreen_getStarted{
    padding: 16px 20px;
    font-size: 1rem;
    color: #fff;
    background-color: #e50914;
    border: none;
    font-weight: 600;
    cursor: pointer;
}

.LoginScreen_input{
    margin: 20px;
}